.selector {
  position: relative; }

.selector__label {
  display: block;
  cursor: pointer;
  padding: 17px 5px 17px 77px;
  font-size: 18px;
  font-weight: 700;
  text-transform: none; }

.selector__input {
  margin-left: 0;
  appearance: none;
  position: absolute;
  width: 55px;
  height: 55px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center; }
  .selector__input::before {
    content: "";
    display: block;
    width: 55px;
    height: 55px;
    background-color: #f2f2f2;
    border-radius: 5%; }
  .selector__input:hover::before {
    background-color: #e5e5e5; }
  .selector__input:active::before {
    background-color: #d5d5d5; }
  .selector__input:checked::after {
    content: "";
    position: absolute;
    width: 22px;
    height: 22px; }
  .selector__input:active:checked::after {
    opacity: 0.3; }
  .selector__input:disabled::before {
    background-color: #fcfcfc;
    cursor: not-allowed; }

.selector__input--radio::before {
  border-radius: 50%; }

.selector__input--radio:checked::after {
  border-radius: 50%;
  background-color: #333333; }

.selector__input--checkbox:checked::after {
  background-image: url(../img/icon/tick.svg);
  background-repeat: no-repeat;
  background-color: inherit; }

